<template>
  <div class="product-nav">
    <form method="get" class="search-form form" name="search-product-by-name-article">
      <div class="field">
        <input type="hidden" name="is_owner" :value="owner">
        <input class="field-style" type="text" placeholder=" " name="search_field" id="search_field" maxlength="" v-model="query">
        <label class="placeholder" for="search_field">Название или артикул</label>
      </div>
      <input name="send-search" class="send-search" @click="searchProduct" value="" type=submit>
    </form>
    <div class="buttons">
      <a href="javascript:void(0)" class="black hover" :class="{active: filter.all}" @click="searchProduct">Все</a>
      <a href="javascript:void(0)" class="black hover" :class="{active: filter.new}" @click="searchNewProduct">Новые</a>
      <a :href="productAdd" class="black hover add">Добавить товар</a>
    </div>
  </div>
</template>

<script>
export default {
  name: "SearchForm",
  data(){
    return {
      query: '',
      filter: {
        all: true,
        new: false,
      }
    }
  },
  computed: {
    isManage(){
      return this.$store.state.manage;
    },
    owner(){
      return this.isManage ? 'N' : 'Y';
    },
    productAdd(){
      return this.isManage ? '/manager/products/add/' : '/partner/products/add/';
    },
    domain(){
      return this.$store.state.domain;
    },
  },
  methods:{
    searchProduct(event){
      if (event) {
        event.preventDefault()
      }
      if (process.env.NODE_ENV === 'production') {
        /*eslint no-undef: "off"*/
        waitCheck();
      }
      this.filter.all = true;
      this.filter.new = false;
      let queryUrl = '';
      if(this.query !== ''){
        queryUrl = this.domain + '/include/ajax_manage.php?search_field='+this.query+'&is_owner='+this.owner+'&PAGEN_1=1';
      }
      else {
        queryUrl = this.domain + '/include/ajax_manage.php?is_owner='+this.owner+'&PAGEN_1=1';
      }
      this.$http.get(queryUrl).then(response => {
        // get body data

        this.$store.dispatch('setProducts', response.data.items)
        this.$store.dispatch('setPage', response.data.page)
        if(response.data.items.length < 10){
          this.$store.dispatch('setShowingNextBtn', false)
        }
        else {
          this.$store.dispatch('setShowingNextBtn', true)
        }
        if (process.env.NODE_ENV === 'production') {
          /*eslint no-undef: "off"*/
          waitCheck('close');
        }
      }, response => {
        // error callback
        console.log(response)
      });
    },
    searchNewProduct(event){
      if (event) {
        event.preventDefault()
      }
      if (process.env.NODE_ENV === 'production') {
        /*eslint no-undef: "off"*/
        waitCheck();
      }
      this.filter.all = false;
      this.filter.new = true;
      this.$http.get('/include/ajax_manage.php?search_field=newProduct&is_owner='+this.owner+'&PAGEN_1=1').then(response => {
        // get body data

        this.$store.dispatch('setProducts', response.data.items)
        this.$store.dispatch('setPage', response.data.page)
        this.$store.dispatch('setShowingNextBtn', false)
        if (process.env.NODE_ENV === 'production') {
          /*eslint no-undef: "off"*/
          waitCheck('close');
        }
      }, response => {
        // error callback
        console.log(response)
      });
    },
  }
};
</script>